<template>
	<van-nav-bar
			:title="title"
			left-text=""
			:left-arrow="showBackArrow"
			@click-left="onBack"
	/>
</template>

<script>
	import {NavBar} from 'vant';

	export default {
		name: 'headerNav',

		components: {
			NavBar,
		},

		props: {
			title: String,
			showBackArrow: {
				type: Boolean,
				default: true
			},
		},

		methods: {
			onBack() {
				history.back();
			},
		}
	}
</script>

<style>

</style>



