<template>
	<van-cell-group class="additional">
		<van-card
				:title="product.GoodsName | ellipsis(15)"
				:num="(isCard ? null : product.GoodsCount)"
				style="background:#fff"
		>
			<template slot="thumb">
				<img :src="product.GoodsImg"/>
			</template>

			<template slot="desc">
				<div>{{product.SerialNumber}}</div>
				<div>{{product.specs}}</div>
			</template>

			<template slot="tags">
				<p class="price">
					￥<span>{{product.Price}}</span>
				</p>
				<van-stepper v-if="isCard" v-model="product.GoodsCount" @change="handleChange" integer :min="1" :max="product.Quota !== -1 ? product.Quota : 10000" input-width="50px"/>
			</template>

			<template slot="num">
				<div v-if="!isCard">
					<span style="margin-right: 15px;">x {{product.GoodsCount}}</span>
					<span v-if="isOrder">小计:￥{{product.totalPrice}}</span>
				</div>
			</template>
		</van-card>
		<slot/>
	</van-cell-group>
</template>

<script>
	export default {
		name: 'product-card',

		props: {
			product: Object,
			isCard: {
				type: Boolean,
				default: false
			},
			isOrder: {
				type: Boolean,
				default: false
			}
		},

		methods: {
			async handleChange(e) {
				let value = e;
				if (!value) {
					value = 1;
				}
				this.product.GoodsCount = value;
				this.product.totalPrice = value * this.product.Price;

				let specs = [];
				this.product.GoodsValueList.forEach(item => {
					let tmp = {
						'CategoryType': item.CategoryType,
						'GoodsValueID': item.GoodsValueID,
					};
					specs.push(tmp);
				});
				await this.$request.updateCartCount(this.product.GoodsID, this.product.GoodsCount, specs);
			},
		}
	}
</script>

<style lang="less">
	.additional {
		.van-cell {
			padding: 0 15px;
			font-size: 12px;
		}
		.van-cell:not(:last-child)::after {
			border: 0;
		}
		.van-card__title {
			font-size: 14px;
		}
		.van-cell__title {
			flex: 10;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
		.van-tag {
			line-height: 12px;
			margin-right: 5px;
		}
		.price {
			color: #e93b3d;
			font-size: 10px;
			overflow: hidden;
			height: 18px;
			span {
				font-size: 16px;
				margin-right: 5px;
			}
			.van-tag {
				font-size: 12px;
			}
		}
		.van-stepper {
			position: absolute;
			bottom: 5px;
			right: 5px;
			&__plus {
				width: 30px;
			}
			&__minus {
				width: 30px;
			}
		}
		.image_tag {
			position: absolute;
			bottom: 0;
			width: 90px;
			height: 20px;
			line-height: 20px;
			font-size: 10px;
			color: #fff;
			text-align: center;
			background-color: rgba(0, 0, 0, .7);
		}
	}

	.additional::after {
		border-color: #f7f7f7;

	}
</style>



